.editPanel {
  max-width: 350px;
  min-width: 350px;
  overflow-y: scroll;
  min-height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background: #f5f5f5;
  -webkit-box-shadow: 0 0 4px 0 #ddd;
          box-shadow: 0 0 4px 0 #ddd;
  padding: 15px;
  z-index: 6000;
}
.editPanel .editPanelActionBar {
  margin-bottom: 15px;
  text-align: right;
}
.editPanel .editPanelActionBar .btn {
  line-height: 0;
  padding: 5px 10px;
}
.editPanel .editPanelActionBar .btn svg {
  width: 12px;
  height: 12px;
}
.editPanel .editPanelActionBar .btn:active {
  outline: none !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
}
.editPanel .editPanelActionBar .btn:focus {
  outline: none !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
}
.editPanel .hyp-edit-panel-wrap {
  height: 100%;
}
.editPanel .hyp-edit-panel-wrap div ul {
  width: 100%;
  text-align: justify;
  background: #eee;
}
.editPanel .hyp-edit-panel-wrap div ul li {
  margin-bottom: 0;
}
.editPanel .hyp-edit-panel-wrap div ul li a {
  font-size: 14px;
  line-height: 14px;
}
.editPanel .hyp-edit-panel-wrap div ul li a.nav-link {
  border: none;
  border-radius: 0;
}
.editPanel .hyp-edit-panel-wrap div ul li a.nav-link .active {
  border: none;
  border-radius: 0;
}
.editPanel .hyp-edit-panel-wrap div ul:focus {
  outline: none;
}
.editPanel .hyp-edit-panel-wrap .tab-content fieldset .form-group .field-wrap select.form-control {
  font-size: 14px;
  padding: 4px 10px;
  height: 30px;
}
.editPanel fieldset {
  background: #fff;
  margin: 5px 0;
  position: relative;
  overflow: hidden;
  display: block;
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, .08);
          box-shadow: 0 0 4px 0 rgba(0, 0, 0, .08);
}
.editPanel fieldset input:active {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.editPanel fieldset input:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.editPanel fieldset select:active {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.editPanel fieldset select:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.editPanel fieldset legend {
  font-size: 14px;
  line-height: 14px;
  font-weight: bold;
  color: #0061cc;
  background: #eee;
  padding: 6px 10px;
  margin: 0;
  border-bottom: 1px solid #dbdbdb;
}
.editPanel fieldset legend:hover {
  cursor: pointer;
}
.editPanel fieldset .form-group {
  padding: 0 10px;
  margin-bottom: 5px;
}
.editPanel fieldset .form-group label {
  font-size: 13px;
  line-height: 15px;
  margin-bottom: 4px;
}
.editPanel fieldset .form-group .field-wrap .wrapper .form-control {
  padding: 4px 10px;
  height: 30px;
}
.editPanel fieldset .form-group .field-wrap .btn-group button {
  padding: 0 6px;
}
.editPanel fieldset .form-group .field-wrap .imageChooser button:active {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.editPanel fieldset .form-group .field-wrap .imageChooser button:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.editPanel fieldset.willcollapse div.form-group {
  display: none;
  -webkit-transition: all .3s ease-in;
  transition: all .3s ease-in;
}
.editPanel fieldset.willcollapse.active div.form-group {
  display: block;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.editPanel .ql-container,
.editPanel .ql-editor {
  height: auto;
}
