.showControl {
  border-color: #007bff !important;
}
.showControl>.vc-action-bar:first-child {
  visibility: visible;
}
.vc-action-bar {
  position: absolute;
  visibility: hidden;
  width: 200px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #007bff;
  clear: both;
  height: 20px;
  z-index: 5000;
}
.vc-action-bar .hyp-btn-wrap {
  line-height: 0;
  padding: 2px 5px;
}
.vc-action-bar .btn {
  line-height: 0;
  padding: 2px;
  background: none;
  color: #fff;
  border: none;
}
.vc-action-bar .btn svg {
  width: 12px;
  height: 12px;
}
.vc-action-bar .btn:hover {
  background: #007bff;
  color: #fff;
}
.vc-action-bar span.editing {
  padding: 2px;
  color: #007bff;
}
.vc-action-bar span.editing svg {
  width: 12px;
  height: 12px;
}
.vc-action-bar span.itemType {
  font-size: 10px;
  color: #eee;
}
.vc-action-bar.forceVisible {
  visibility: visible !important;
  background: #aaa;
}
.vc-action-bar.editing {
  visibility: visible !important;
  background: #007bff;
}
