.hyp-block-model .modal-header {
  background: #007bff;
  color: #fff;
  padding: 10px 15px;
}
.hyp-block-model .modal-body .row {
  margin-left: 0;
  margin-right: 0;
}
.hyp-block-model .modal-body .row .hyp-block-model-col {
  padding: 0;
  margin-bottom: 10px;
  border-right: 1px solid rgba(0, 0, 0, .08);
}
.hyp-block-model .modal-body .row .hyp-block-model-col:last-child {
  border-right: none;
}
.hyp-block-model .modal-body .row .hyp-single-block {
  -webkit-box-shadow: 1px 1px 1px 0 rgba(0, 0, 0, .08);
          box-shadow: 1px 1px 1px 0 rgba(0, 0, 0, .08);
  border: none;
  background: #f5f5f5;
  border-radius: 0;
  -webkit-transition: all .3s ease-in;
  transition: all .3s ease-in;
  padding: 10px 15px;
}
.hyp-block-model .modal-body .row .hyp-single-block h6 {
  font-size: 16px;
  line-height: 16px;
  color: #0061cc;
  font-weight: bold;
  margin-bottom: 2px;
}
.hyp-block-model .modal-body .row .hyp-single-block p {
  font-size: 13px;
  line-height: 16px;
  color: #8c8c8c;
  height: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  max-height: 16px;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.hyp-block-model .modal-body .row .hyp-single-block:hover {
  cursor: pointer;
  -webkit-box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .1);
          box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .1);
}
.hyp-block-model .modal-footer {
  display: none;
}
