.addBlocksButtonWrapper {
  text-align: center;
}
.addBlocksButtonWrapper .btn {
  padding: 5px;
  line-height: 0;
  color: #dcdbdb;
  background-color: #f7f7f7;
  border-color: #ececec;
}
.addBlocksButtonWrapper .btn svg {
  width: 15px;
  height: 15px;
}
.addBlocksButtonWrapper .btn:active {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  outline: none !important;
}
.addBlocksButtonWrapper .btn:focus {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  outline: none !important;
}
p.noBlockMessage {
  font-size: 11px;
  font-style: italic;
  margin-bottom: 2px;
  text-align: center;
  color: #e1e0e0;
}
